body {
    font-family: "游ゴシック",YuGothic,"ヒラギノ角ゴ Pro W3","Hiragino Kaku Gothic Pro","メイリオ",Meiryo,Osaka,"ＭＳ Ｐゴシック","MS PGothic",sans-serif;
}
.App {
    max-width: 1000px;
    margin: 0 auto;
}
a {
    text-decoration: none;
    color:#fff;
}
a:link { color: #fff; }
.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  /*background-color: #000;

  height: 150px;
  padding: 20px;
  color: white;*/
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

.adress {
    width:100%;
}

.flex-container {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row; /* デフォルト値 */
    -ms-flex-align: stretch;
        align-items: stretch; /* デフォルト値 */
    -ms-flex-pack: start;
        justify-content: flex-start; /* デフォルト値 */
    -ms-flex-wrap: nowrap;
        flex-wrap: nowrap; /* デフォルト値 */
    -ms-flex-line-pack: strech;
        align-content: strech; /* デフォルト値 */
}
.ancestor{
    -ms-flex-pack: justify;
        justify-content: space-between;
    border-bottom: 1px solid #E8E8E8;
    margin-bottom: 10px;
}
.ancestor > div {
    -ms-flex-positive: 1;
        flex-grow: 1;
    text-align: center;
    padding: 5px 0;
}
.descendants{
    border-top: 1px solid #E8E8E8;
    text-align:center;
    padding: 5px 0;
    margin-top: 10px;
}
.line {
    border-left: 1px solid #E8E8E8;
}



.app-bar{

}
.app-bar__header{
    max-height: 50px !important;
    min-height: 40px !important;
}
.self-introduction {
    width:100%;
    padding: 0 15px;
}
.self-introduction__img{
    height: 100px;
    -ms-flex-negative: 0;
        flex-shrink: 0;
    -ms-flex-preferred-size: 100px;
        flex-basis: 100px;
}
.self-introduction__info h3{
    font-size:1.3em;
}
.self-introduction__info{
    margin-top: auto;
    padding-left: 0.5rem;
}
.self-introduction__link{
    margin-left: auto;
    -ms-flex-flow: column;
        flex-flow: column;
}
.self-introduction__link >:last-child {
    margin-top: auto;
    margin-left: auto;
}
.self-introduction__link__partner{
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
}
.partner{
    width: 2rem;
    height: 2rem;
    border: 1px solid grey;
    border-radius: 2rem;
    line-height: 2rem;
    text-indent: 0.5em;
}
.add-adress{
    text-align:center;
    width:90%;
    display:block;
    margin:20px auto !important;
}
.user-event {
    border-bottom: 5px solid #F5EEE5;
    -ms-flex-align: center;
        align-items: center;
    background-color : #fff;
}
.event-year {
    height: 35px;
    font-size: 2rem;
    color: #fff;
    font-weight: bold;
}
.user-event__info{
    font-size:0.8rem;
    padding-left:10px;
}
.user-event__img{
    margin-left:auto;
    -ms-flex-negative: 0;
        flex-shrink: 0;
    -ms-flex-preferred-size: 100px;
        flex-basis: 100px;
}
.adress {
    border-bottom: 1px solid #E8E8E8;
    -ms-flex-align: center;
        align-items: center;
    padding: 10px 0 10px 0;
}
.adress input {
    width: 20px;
}
.adress__next{
    margin-left:auto;
}
.adress__info{
    font-size:0.8rem;
    padding-left:10px;
}
.app-footer{
    background-color: #000;
}



